body {
  overflow: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navBar {
  overflow: hidden;
  border-radius: 0.2em;
  position: relative;
  height: 10%;
  width: 100%;
  background-color: #00ffc4;
  box-shadow: 0em 1.3em 1.1em rgba(0, 0, 0, 0.25);
}

.navLogo {
  vertical-align: middle;
  padding: 0.5em;
  padding-left: 5%;
  float: left;
  display: relative;
  width: auto;
  max-width: 8%;
}

.aboutBtn {
  border: solid 3px;
  margin: 0.5em;
  padding: 0.1em;
  margin-right: 3em;
  float: right;
  position: relative;
  width: 5em;
  height: 2em;
  max-width: 8%;
  box-shadow: 4px 6px 1px 1px rgba(0, 0, 0, 0.25);
}

.backgroundImg {
  overflow: hidden;
  height: 20rem;
  position: fixed;
  bottom: 0;
  right: 0;
}

.cloud-one {
  position: fixed;
  left: 0;
  z-index: -100;
  margin-top: -4em;
  margin-left: -3em;
  float: left;
  width: 20em;
}

.cloud-two {
  margin-top: -9em;
  margin-right: -7em;
  float: right;
  width: 25em;
}

.landingPage {
  overflow: hidden;
}

.landingQuote {
  font-family: 'Gloria Hallelujah';
  font-size: 1.2em;
  font-style: bold;
}

.quoteBox {
  overflow: hidden;
  position: relative;
  float: right;
  margin-right: 8em;
  width: fit-content;
}

.headBox {
  overflow: hidden;
  position: relative;
  width: fit-content;
  height: fit-content;
}

.landingHeader {
  width: 20em;
  font-size: xx-large;
  font-family: 'Nunito';
}

.landingHeader2 {
  width: 20em;
  font-size: x-large;
  font-family: 'Nunito';
}

.hlWord {
  background: black;
  color: white;
  margin: 0.1em;
  padding-left: 0.3em;
  padding-right: 0.3em;
}

.letsGobtn {
  font-family: 'Nunito';
  font-size: larger;
  width: fit-content;
  height: fit-content;
  padding: 0.3em;
  padding-left: 1em;
  padding-right: 1em;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #b6ffee 100%),
    #00ffc4;
  border-radius: 50px;
}

/* Optimization for bigger screens */
@media screen and (min-width: 1420px) {
  .pruple-tint {
    font-size: 1.5rem;
  }
  .logo {
    max-width: 15.2rem;
  }
}

@media screen and (min-width: 1600px) {
  .pruple-tint {
    font-size: 1.3rem;
  }
  .logo {
    max-width: 15.25rem;
  }
}

@media screen and (min-width: 1920px) {
  .pruple-tint {
    font-size: 1.6rem;
  }
  .logo {
    max-width: 15.3rem;
  }
}

@media screen and (min-width: 2200px) {
  .pruple-tint {
    font-size: 2rem;
  }
  .logo {
    max-width: 15.35rem;
  }
}

@media screen and (min-width: 2700px) {
  .pruple-tint {
    font-size: 2.5rem;
  }
  .logo {
    max-width: 15.35rem;
  }
}
